* {
	outline: none;
	box-sizing: border-box;
}
*::before, *::after {
	box-sizing: border-box;
}
html {
	font-size: 16px;
  height: 100%;
  width: 100%;
	@media (max-width: 1024px) {
		font-size: 15px;
	}
	@media (max-width: 768px) {
		font-size: 14px;
	}
	@media (max-width: 480px) {
		font-size: 12px;
	}
}
body {
	background-color: #333;
	font-family: sans-serif;
	margin: 0;
  width: 100%;
	min-height: 100vh;
	scroll-behavior: smooth;
	font-size: 1rem;
}

.header {
  height: 100vh;
  display: flex;
}
.main-img {
  height: 100%;
  position: absolute;
  right: 0;
  top: 0;
  width: 100%;
  object-fit: cover;
  object-position: center;
}
.nav {

  z-index: 10;
  width: 40%;
  height: 20%;
  color: #101010;
  display: block;
  background-color: #fff;
}
.menu {
  display: flex;
  align-items: center;
  &_item {
    list-style: none;
    margin-right: 40px;
		cursor: pointer;
		&:hover {
			color: #4a778e;
		}
  }
}
.nav-closed {
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: space-around;
  height: 20%;
  width: 40%;

}
.nav-open {
  position: absolute;
  top: 20%;
  width: 40%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
  opacity: 0;
  pointer-events: none;
}
.section {
  display: flex;
  width: 100%;
  justify-content: space-around;
  align-items: center;
}
.subtitle {
  font-size: 40px;
}
.submenu {
  &_item {
    list-style: none;
    margin-bottom: 10px;
  }
  &_link {
    text-decoration: none;
    color: #101010;
    font-size: 18px;
		&:hover {
			color: #4a778e;
		}
  }
}
.img {
  display: flex;
  justify-content: space-around;
  width: 100%;
  &_item {
    width: 250px;
  }
}
.title {
  position: absolute;
  left: 100px;
  bottom: 50px;
  color: #fff;
  font-size: 60px;
}
